/* 04 - GLOBAL SETTINGS */
/*-------------------------------------------------------------------------------------------------------------------------------*/

/*typography*/
.titel-left {
  position: relative;
}
.titel-left span {
  color: #fbc011;
}
.titel-left:after {
  position: absolute;
  left: -25px;
  top: 0px;
  content: "";
  width: 5px;
  height: 100%;
}
.titel-top {
  position: relative;
}
.titel-top:after {
  position: absolute;
  left: 50%;
  top: -25px;
  content: "";
  width: 30px;
  margin-left: -15px;
  height: 5px;
  background: #fbc011;
}
.h1,
.h2,
.h4 {
  text-transform: uppercase;
  color: #222222;
}
.h1 {
  font-size: 56px;
  margin-bottom: 40px;
}
.h2 {
  font-size: 46px;
  line-height: 46px;
  margin-bottom: 40px;
  color: #717171;
  border-bottom: 5px solid #ccc;
}
.h3 {
  font-size: 30px;
  font-weight: normal;
  text-transform: uppercase;
  color: #222222;
}
.h4 {
  font-size: 20px;
  margin-bottom: 20px;
}
.h5 {
  font-size: 16px;
  text-transform: uppercase;
  color: #222222;
  margin-bottom: 20px;
}
.small {
  font-size: 14px;
  line-height: 24px;
}
.normall {
  font-size: 16px;
  line-height: 28px;
}
.big {
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 70px;
}
article {
  position: relative;
  float: left;
  width: 100%;
}
article p {
  color: #777777;
  padding-bottom: 40px;
}
ul li {
  color: inherit;
  position: relative;
  list-style: none;
}
/* ul li:after{position: absolute; left: 0px; top: 12px; width: 3px; height: 3px; border-radius: 5px; content: "";} */
.white {
  color: #fff;
}
.black {
  color: #222222;
}
/*background*/
.bg {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-position: 50% 0px;
  background-size: cover;
  position: fixed;
  background-attachment: scroll;
}
.bg img {
  position: absolute;
}
.bg video {
  position: absolute;
}
.bg-span {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.57);
}
.bg-span.light {
  background: rgba(0, 0, 0, 0.35);
}
.clip {
  clip: rect(auto, auto, auto, auto);
  height: 100%;
  position: absolute;
  width: 100%;
  left: 0px;
  top: 0px;
}

.wrapper {
  display: flex;
  align-items: stretch;
  width: 100%;
  height: auto;
  margin: 0px auto;
  padding: 0px;
  position: relative;
  overflow: hidden;
}
.ap-com {
  width: 100%;
  float: left;
  position: relative;
}
.content-wrapper {
  padding-left: 300px;
}

/*==== Sidebar css =======*/
.sidebar-panel {
  width: 290px;
  z-index: 1005;
  background: #fff;
  bottom: 0;
  margin-top: 0;
  overflow: auto;
  position: fixed;
  left: 0px;
  top: 0px; /*box-shadow: 0 2px 4px rgb(15 34 58 / 12%);*/
  padding: 15px 0px;
  box-shadow: 0 3px 1px -2px rgb(0 0 0 / 20%), 0 2px 2px 0 rgb(0 0 0 / 14%),
    0 1px 5px 0 rgb(0 0 0 / 12%);
}
.logo-sidebar {
  position: relative;
  width: 100%;
  padding: 20px 35px 0px;
}
.logo-sidebar a {
  display: block;
}
.logo-sidebar a img {
  max-width: 100%;
  display: block;
}
.creatdiv {
  padding: 35px;
  width: 100%;
  position: relative;
}
.creatdiv i {
  margin-right: 20px;
}
.creatdiv a {
  display: block;
  padding: 20px;
  text-align: center;
  background-color: #fc7a1e;
  color: #fff;
  border-radius: 6px;
  box-shadow: 0 3px 1px -2px rgb(0 0 0 / 20%), 0 2px 2px 0 rgb(0 0 0 / 14%),
    0 1px 5px 0 rgb(0 0 0 / 12%);
}
.creatdiv a:hover {
}
.scroll-sidebar {
  position: relative;
}
.scroll-sidebar ul {
  list-style: none;
  display: block;
}
.scroll-sidebar ul li {
  display: block;
  list-style: none;
  margin-bottom: 10px;
}
.scroll-sidebar ul li:last-child {
  margin-bottom: 0px;
}
.scroll-sidebar ul li a {
  display: block;
  padding: 10px 15px 10px 30px;
  text-decoration: none;
  color: #202020;
}
.scroll-sidebar ul li a span {
  display: inline-block;
  vertical-align: middle; /*width: 35px;height: 35px;line-height: 35px;border-radius: 100%;*/
  text-align: center;
  margin-right: 10px;
  padding: 8px;
}
.scroll-sidebar ul li a svg {
  width: 17px;
  height: 17px;
  display: inline-block;
  vertical-align: middle;
  margin: 0px auto;
}
.scroll-sidebar ul li a:hover {
  color: #000;
}
.scroll-sidebar ul li a.active {
  color: #202020;
  font-weight: 500;
  background-color: #f7f7f7;
  border-left: 5px solid #fc7a1e;
}
.scroll-sidebar ul li a.active i{
  color:#fc7a1e;
}
.bs-icon.bs-icon-rounded {
  border-radius: 0.5rem;
}

.bs-icon.bs-icon-primary {
  color: var(--bs-white);
  background: var(--bs-primary);
  background: #fc7a1e;
  color: #fff;
}
.bs-icon {
  --bs-icon-size: .75rem;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  font-size: var(--bs-icon-size);
  width: calc(var(--bs-icon-size) * 2);
  height: calc(var(--bs-icon-size) * 2);
  color: var(--bs-primary);
}
.bs-icon-sm {
  --bs-icon-size: 1rem;
}
.sidebar-img {
  position: absolute;
  bottom: 0;
  text-align: center;
