  height: 35px;
  border-radius: 100%;
  overflow: hidden;
  border: 2px solid #fff;
  display: block;
  margin-left: -12px;
}
.circle-user ul li:nth-child(1) a {
  margin-left: 0px;
}
.circle-user ul li .useranh-count {
  text-align: center;
  color: #fff;
  background-color: #ff5e3a;
  font-size: 10px;
  font-weight: 800;
  line-height: 32px;
  opacity: 0.8;
  padding: 0px;
}
.circle-user ul li a img {
  max-width: 100%;
  height: auto;
  display: inline-block;
}

.action-div {
  position: relative;
}
.action-div .dropdown-toggle {
  outline: none;
  border: none;
  box-shadow: unset;
  background-color: transparent;
  display: inline-block;
  width: 35px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  font-size: 18px;
  color: #565454;
}
.action-div .dropdown-toggle:hover,
.action-div .dropdown-toggle:focus {
  outline: none;
  border: none;
  box-shadow: unset;
  background-color: transparent;
}
.action-div .dropdown-toggle:hover {
  color: #000;
}
.action-div .dropdown-toggle:after {
  display: none;
}
.action-div .dropdown-menu {
  background-color: #fff;
  list-style: none;
}
.action-div .dropdown-menu li {
  list-style: none;
  display: block;
  margin-bottom: 10px;
}
.action-div .dropdown-menu li:last-child {
  margin-bottom: 0px;
}
.action-div .dropdown-menu li .dropdown-item {
  color: #777777;
  display: block;
  font-size: 14px;
}
.action-div .dropdown-menu li .dropdown-item i {
  margin-right: 10px;
}
.action-div .dropdown-menu li .dropdown-item:hover {
  color: #000;
  background-color: transparent;
}
#rememberMe{
  width:20px!important;
  height: 20px;
  margin-right: 10px;
}
