/* 04 - GLOBAL SETTINGS */
/*-------------------------------------------------------------------------------------------------------------------------------*/

/*typography*/
.titel-left {
  position: relative;
}
.titel-left span {
  color: #fbc011;
}
.titel-left:after {
  position: absolute;
  left: -25px;
  top: 0px;
  content: "";
  width: 5px;
  height: 100%;
}
.titel-top {
  position: relative;
}
.titel-top:after {
  position: absolute;
  left: 50%;
  top: -25px;
  content: "";
  width: 30px;
  margin-left: -15px;
  height: 5px;
  background: #fbc011;
}
.h1,
.h2,
.h4 {
  text-transform: uppercase;
  color: #222222;
}
.h1 {
  font-size: 56px;
  margin-bottom: 40px;
}
.h2 {
  font-size: 46px;
  line-height: 46px;
  margin-bottom: 40px;
  color: #717171;
  border-bottom: 5px solid #ccc;
}
.h3 {
  font-size: 30px;
  font-weight: normal;
  text-transform: uppercase;
  color: #222222;
}
.h4 {
  font-size: 20px;
  margin-bottom: 20px;
}
.h5 {
  font-size: 16px;
  text-transform: uppercase;
  color: #222222;
  margin-bottom: 20px;
}
.small {
  font-size: 14px;
  line-height: 24px;
}
.normall {
  font-size: 16px;
  line-height: 28px;
}
.big {
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 70px;
}
article {
  position: relative;
  float: left;
  width: 100%;
}
article p {
  color: #777777;
  padding-bottom: 40px;
}
ul li {
  color: inherit;
  position: relative;
  list-style: none;
}
/* ul li:after{position: absolute; left: 0px; top: 12px; width: 3px; height: 3px; border-radius: 5px; content: "";} */
.white {
  color: #fff;
}
.black {
  color: #222222;
}
/*background*/
.bg {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-position: 50% 0px;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  position: fixed;
  background-attachment: scroll;
}
.bg img {
  position: absolute;
}
.bg video {
  position: absolute;
}
.bg-span {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.57);
}
.bg-span.light {
  background: rgba(0, 0, 0, 0.35);
}
.clip {
  clip: rect(auto, auto, auto, auto);
  height: 100%;
  position: absolute;
  width: 100%;
  left: 0px;
  top: 0px;
}

.wrapper {
  display: flex;
  align-items: stretch;
  width: 100%;
  height: auto;
  margin: 0px auto;
  padding: 0px;
  position: relative;
  overflow: hidden;
}
.ap-com {
  width: 100%;
  float: left;
  position: relative;
}
.content-wrapper {
  padding-left: 300px;
}

