/*==== Header Css ===== */
header {
  position: fixed;
  left: 300px;
  top: 0px;
  right: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  z-index: 1;
}
.header-left {
  position: relative;
}
.header-left h2 {
  color: #565454;
  font-size: 24px;
}
.header-left h2 span {
  font-weight: 600;
  color: #000;
}
.header-left ul {
  list-style: none;
  display: block;
  margin-top: 15px;
}
.header-left ul li {
  display: inline-block;
  list-style: none;
  padding-right: 10px;
}
.header-left ul li i {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
}
.header-left ul li:last-child i {
  display: none;
  padding-right: 0px;
}
.header-left ul li a {
  display: inline-block;
  color: #565454;
  vertical-align: middle;
}
.header-left ul li a.active {
  color: #000;
}
.header-right {
  position: relative;
}
.header-right ul {
  list-style: none;
  display: flex;
  align-items: center;
  margin-bottom: 0;
}
.header-right ul li {
  display: inline-block;
  list-style: none;
  margin-left: 25px;
}
.header-right ul li a {
  display: block;
  font-size: 18px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #565454;
  border-radius: 4px;
}
.header-right ul li a:hover,
.header-right ul li a:focus {
  color: #000;
  background-color: #f7f7f7;
}
.header-right ul .img-li {
  padding: 0px;
}
.header-right ul .img-li a {
  padding: 0px;
  width: auto;
  height: auto;
  line-height: unset;
}
.header-right ul .img-li a img {
  max-width: 100%;
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 6px;
  box-shadow: 0 3px 1px -2px rgb(0 0 0 / 20%), 0 2px 2px 0 rgb(0 0 0 / 14%),
    0 1px 5px 0 rgb(0 0 0 / 12%);
}
.header-right ul li .dropdown-menu {
  display: none;
  background-color: #fff;
  list-style: none;
  /* transform: translate(-110px, 65px) !important; */
  box-shadow: 0 5px 25px rgb(39 55 76 / 20%);
}
.header-right ul li .dropdown-menu .title-li {
  padding: 10px;
  font-weight: 600;
  color: #000;
  font-size: 18px;
}
.header-right ul li .dropdown-menu.show {
  display: block;
}
.header-right ul li .dropdown-toggle:after {
  display: none;
}
.header-right ul li .dropdown-menu li {
  list-style: none;
  display: block;
  margin-left: 0px;
  margin-bottom: 10px;
}
.header-right ul li .dropdown-menu li:last-child {
  margin-bottom: 0px;
}
.header-right ul li .profile-ul {
}
.profile-ul li .dropdown-item {
  color: #777777;
  display: block;
  font-size: 14px;
  text-align: left;
  padding: 0.25rem 1rem !important;
}
.profile-ul li .dropdown-item i {
  margin-right: 10px;
}
.profile-ul li .dropdown-item:hover {
  color: #000;
  background-color: transparent !important;
}
.header-right ul .notoi-li {
  position: static;
}
.header-right ul .notoi-li .nofify-ul {
  min-width: 25rem;
  /* transform: translate(-170px, 65px) !important; */
}
.nofify-ul li {
  margin-bottom: 15px;
}
.nofify-ul li:last-child {
  margin-bottom: 0px;
}
.nofify-ul li .dropdown-item {
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  width: auto;
  height: auto;
  line-height: unset;
  text-align: unset;
  align-items: center;
}
.nofify-ul li .dropdown-item:hover,
.nofify-ul li .dropdown-item:focus {
  color: unset;
  background-color: transparent;
}
